import sizes from './sizes';
//import bg from './bg.svg';
import bgNew from './bgNew.svg';
export default {
    "@global":{
        ".fade-exit":{
            opacity:"1"
        },
        ".fade-exit-active":{
            opacity:"0",
            transition:"opacity 500ms ease-out"
        }
    },
    root: {
        backgroundColor: "blue",
        height: "100vh",
        display: "flex",
        alignItems: "flex-start",
        justifyContent: "center",
        //background by SvgBackgrounds.com
        background:`url(${bgNew})`,
        overflow:"scroll"
    },
    heading:{
        fontSize:"3rem",
        fontFamily:"serif"
    },
    createBtn:{
        fontSize:"2rem",
        fontFamily:"Lucida Console",
        fontStyle:"italic",
        [sizes.down("xs")]: {
            marginLeft: "10px"
        }
        
    },
    resetBtn:{
        [sizes.down("xs")]: {
            opacity:"0"
        }
    },
    secondaryNav:{
        display:"inline-block",
        justifyContent:"space-between",
        alignItems:"center",
        [sizes.down("xs")]: {
            opacity:"0"
        }

    },
    container: {
        width: "50%",
        display: "flex",
        alignItems: "flex-start",
        flexDirection: "column",
        flexWrap: "wrap",
        [sizes.down("xl")]: {
            width: "80%"
        },
        [sizes.down("xs")]: {
            width: "75%"
        }
    },
    nav: {
        display: "flex",
        width: "100%",
        justifyContent: "space-between",
        alignItems: "center",
        color: "white",
        "& a": {
            color: "white"
        }
    },
    palettes: {
        boxSizing: "border-box",
        width: "100%",
        display: "grid",
        gridTemplateColumns: "repeat(3, 30%)",
        gridGap: "2.5rem",
        [sizes.down("md")]: {
            gridTemplateColumns: "repeat(2, 50%)",
        },
        [sizes.down("xs")]: {
            gridTemplateColumns: "repeat(1, 100%)",
            gridGap: "1.5rem",
        }
    }
};